<template>
  <div>
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      fixed
      @click-left="onClickLeft"
    />

    <section class="van-doc-demo-section">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

        <van-row class="demo_title">
          <van-col span="7">Tenor</van-col>
          <van-col span="8">Bid/Vol.Bid</van-col>
          <van-col span="8">Ofr/Vol.Ofr</van-col>
        </van-row>

        <van-collapse v-model="activeNames">
          <van-collapse-item :title="item.title" :name="index" v-for="(item,index) in list">
            <van-row class="demo_item">
              <van-col span="6">3M</van-col>
              <van-col span="5">2.7834</van-col>
              <van-col span="4">200</van-col>
              <van-col span="5">2.7834</van-col>
              <van-col span="4">200</van-col>
            </van-row>
          </van-collapse-item>
        </van-collapse>

      </van-pull-refresh>
    </section>


  </div>
</template>

<script>
  export default {
    name: "interestRate",
    data() {
      return {
        title: '利率互换',
        isLoading: false,
        activeNames: [0, 1, 2, 3, 4],
        list: [
          {title: 'IRS-FR007', value: '测试测试测试'},
          {title: 'IRS-FR007', value: '测试测试测试'},
          {title: 'IRS-FR007', value: '测试测试测试'},
          {title: 'IRS-FR007', value: '测试测试测试'},
          {title: 'IRS-FR007', value: '测试测试测试'},
        ]
      };
    },
    methods:{
      onClickLeft(){
        this.$router.back();
      },
      onRefresh() {
        setTimeout(() => {
          this.$toast('刷新成功');
          this.isLoading = false;
          this.count++;
        }, 500);
      }
    }
  }
</script>

<style scoped>

  .demo_title {
    background: rgba(255, 255, 255, 1);
    margin-bottom: 10px;
  }
  .demo_title div {
    text-align: center;
    font-size: 15.5px;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: rgba(117, 117, 117, 1);
    line-height: 40px;
  }

  .demo_item div {
    text-align: center;
    font-size: 14px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(221, 169, 68, 1);
    line-height: 20px;

  }
  .demo_item div:first-child {
    color: rgba(124, 124, 125, 1);
  }


</style>



